/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import domReady from '@wordpress/dom-ready';
import { render } from '@compatible';

domReady(() => {
	setTimeout(() => {
		// ヘッダーツールバーを取得
		const headerToolbar = document.querySelector('.edit-post-header__toolbar');

		if (null === headerToolbar) return;

		// ヘッダーツールバーにdiv追加
		headerToolbar.insertAdjacentHTML(
			'beforeend',
			'<div class="ark-custom-header-toolbar"></div>'
		);

		let headerLink = '';
		if (window.arkbBlockVars?.assetsUrl) {
			headerLink = (
				<a
					href='https://patterns.arkhe-theme.com/'
					className='__patternsLink'
					target='_blank'
					rel='noopener noreferrer'
					title='Arkhe Patterns Library'
				>
					<img
						className='__img'
						src={window.arkbBlockVars.assetsUrl + '/img/arkp-link.png'}
						alt='Arkhe Patterns Library'
						width='260'
						height='80'
					/>
				</a>
			);
		}

		// ここから先は設定がオンの時のみ
		if (!window.arkbBlockVars?.disableHeaderLink) {
			// 投稿データ
			const currentPost = wp.data.select('core/editor').getCurrentPost();
			if (!currentPost) return;

			// URLを取得する
			const currentPostLink = currentPost?.link; // currentPost.permalink_template も同じ？
			if (!currentPostLink) return;

			// 投稿タイプ名を取得
			const postType = currentPost?.type; // wp.data.select('core/editor').getCurrentPostType();

			// 投稿タイプデータを取得
			const postTypeObj = wp.data.select('core').getPostType(postType);

			const viewable = postTypeObj?.viewable;
			if (!viewable) return;

			// 表示テキスト
			let linkText = '';
			if ('post' === postType) {
				linkText = __('View Post', 'arkhe-blocks');
			} else {
				linkText = __('View Page', 'arkhe-blocks');
			}

			headerLink = (
				<>
					<a
						href={currentPostLink}
						target='_blank'
						rel='noopener noreferrer'
						className='__postLink'
					>
						{linkText}
					</a>
					{headerLink}
				</>
			);
		}

		render(document.querySelector('.ark-custom-header-toolbar'), headerLink);
	}, 50); // 速すぎると6.2で動かないケースがあるので50くらいにしてる
});
